body{
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
  background: #f0f0f0;
  color: #333;
}
.App{
  text-align: center;
}
.App-header{
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px);
  color: white;
}
header {
  background: #333;
  color: #fff;
  padding: 10px 0;
  text-align: center;
}
nav ul {
  list-style: none;
  padding: 0;
}
nav ul li{
  display: inline;
  margin: 0 10px;
}
nav ul li a{
  color: #fff;
  text-decoration: none;
}
section{
  padding: 50px;
  text-align: left;
}
section h2{
  font-size: 2em;
  margin-bottom: 20px;
}
footer {
  background: #333;
  color: #fff;
  text-align: center;
  padding: 10px 0;
  position: absolute;
  width: 100%;
  bottom: 0;
}